.training {
  padding-top: 100px;
  background-color: var(--light-color);

  @include small-tablet {
    padding-top: 50px;
  }

  &__container {
    padding: 48px 116px 62px;
    background: #21093a;
    border-radius: 20px;

    @include tablet {
      padding: 48px;
    }

    @include mobile {
      padding: 20px;
    }
  }

  &__title {
    margin-bottom: 40px;
    color: var(--light-color);
  }

  &__item {
    position: relative;
    min-height: 200px;
    padding: 24px 20px;
    border: 1px solid var(--accent-color);
    border-radius: 16px;
    grid-column: 6 span;
    z-index: 1;

    @include small-tablet {
      grid-column: 12 span;
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 80px;
      height: 80px;
      background-color: var(--accent-color);
      opacity: 0.6;
      filter: blur(30px);
      z-index: -1;
    }

    @include hover {
      &::after {
        @include kf-zoom;

        animation: kf-zoom 4s infinite;
      }
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 24px;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 24px;
    line-height: 31px;
    color: var(--selago-color);
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    color: var(--selago-color);
  }

  &__sublist {
    padding-top: 7px;
    padding-left: 24px;
  }

  &__subitem {
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    color: var(--selago-color);
  }
}
